
@font-face {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 400;
  src: url('/css/fonts/kanit-v3-thai_latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Kanit Regular'), local('Kanit-Regular'),
      url('/css/fonts/kanit-v3-thai_latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
      url('/css/fonts/kanit-v3-thai_latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
      url('/css/fonts/kanit-v3-thai_latin-regular.woff') format('woff'), /* Modern Browsers */
      url('/css/fonts/kanit-v3-thai_latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
      url('/css/fonts/kanit-v3-thai_latin-regular.svg#Kanit') format('svg'); /* Legacy iOS */
}
 .header-text h1{
  font-size: 3.9em;
  margin-top: 5px;
}


.app{
  margin-top: 6px;
}
.app-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}


.text-header , .footer-line{
  background: #d078ff;
  background: -webkit-gradient(linear ,left top, right bottom ,from(#d078ff) , to(#92acff));
  background: -webkit-linear-gradient(top left ,#d078ff , #92acff);
  background: -o-linear-gradient(top left ,#d078ff , #92acff);
  background: linear-gradient(to bottom right ,#d078ff , #92acff); /* Standard syntax (must be last) */
  color: #F1F1F1;
}

.footer-line {
  width: 100%;
  height: 6px;
}




h1, h2, h3, h4, h5, h6 ,.btn {
  font-family: 'Kanit', sans-serif;
  }

.top-menu{
  margin-top: 5px;
  background: #333;
  color:#f1f1f1;
  height: 40px;
}

.top-menu a{
  color:#959998;
   font-size: 1.2em;
}


.header-text-warp{
   height: 100px;
}
.header-logo{
  width: 80% !important;
}



.content{
  margin-top:60px;
}


.detail-row{
  margin-bottom: 30px;
}


.detail-title{
  font-weight: bold !important;;
}


.small-text{
  font-size: 0.8em;
}


.detail p{
  font-family:  -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif !important; 
  color:#4c4c4c;
}

.left-img{
  display: inline-block;
  width:40%;
}

.h-line{
 border:  1px  dotted  #000;
}



.btn-puple {
  color: #fff;
  background-color: #963cbf;
  border-color: #963cbf;
  cursor: pointer;
}

.btn-puple:hover {
  color: #fff;
  background-color: #CE7AFF;
  border-color: #CE7AFF;
  cursor: pointer;
}

.btn-puple:focus, .btn-primary.focus {
  -webkit-box-shadow: 0 0 0 3px rgba(234, 0, 255, 0.5);
          box-shadow: 0 0 0 3px rgba(234, 0, 255, 0.5);
}


.btn-big{
  padding: 12px 50px !important;
}

footer p{
  font-size: 0.8rem;
}


